.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  padding: 0;
  border: none;
  outline: none;
  /* background-color: #eee; */
  background-color: #c0bfbf;
  box-shadow: none;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, sans-serif;
}

main {
  background-color: white;
  padding: 10px 40px 40px 40px;
  display: block;
  text-align: left;
  max-width: 700px;
  margin: 40px auto auto auto;
  border-radius: 10px;
}

header {
  text-align: center;
}

h1 {
  font-size: 40px;
  color: #191516;
  font-weight: bold;
}

form {
  display: flex;
  flex-direction: column;
  font-size: 22px;
}

#name, #email, #number, #dropdown {
  outline: none;
  width: 100%;
  padding: 7px;
  font-size: 16px;
  border: 1px solid #dee2e6;
  border-radius: 3px;
  background-color: white;
  box-sizing: border-box;
}

#dropdown, option {
  outline: none;
}

select {
  padding: 7px;
}

textarea {
  display: block;
}

.form-group {
  padding: 8px 0;
  margin-bottom: 2px;
}

.form-group>label {
  margin-bottom: 8px;
  color: #191516;
  display: block;
  font-weight: 500;
}

[type="checkbox"]+label>span, [type="radio"]+label {
  font-size: 16px;
}

[type="checkbox"], [type="radio"] {
  margin: 0 8px 0 0;
  box-sizing: border-box;
  min-width: 18px;
  min-height: 18px;
}

/*hide the original checkbox and radio button*/

[type="checkbox"] , [type="radio"] {
  opacity: 0;
  pointer-events: none;
}


.radio-group, .checkbox-group {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  position: relative;
}

[type="radio"]+label {
  margin: 0 5px 0 0;
  font-size: 16px;
  position: relative;
  cursor: pointer;
}

[type="checkbox"]+label {
  margin: 0 5px 0 0;
  font-size: 16px;
  position: relative;
  cursor: pointer;
}

/* this is the custom box for the checkbox*/

[type="checkbox"]+label::before {
  content: '';
  min-width: 18px;
  min-height: 18px;
  position: absolute;
  left: -26px;
  border-radius: 3px;
  /* top: 5px; */
  background-color: #eee;
  transition: all 0.4s;
}

/* custom checkmark made using only css*/

