/* this will unhide the custom checkmark */

/* [type="checkbox"]:checked +label::after {
  min-width: 5px;
  min-height: 10px;
} */

/* this is the svg for the checkmark */

[type="checkbox"]+label svg {
  position: absolute;
  width: 28px;
  left: -31px;
  top: -5px;
}

/* this is the polyline for the checkmark svg */

[type="checkbox"]+label svg polyline {
  stroke: white;
  stroke-width: 8;
  fill: none;
  stroke-dasharray: 70;
  stroke-dashoffset: 70;
  transition: stroke-dashoffset 0.4s;
}

/* change the color of custom checkbox when the original checkbox is in checked state */

[type="checkbox"]:checked+label::before {
  background-color: #191516;
}

/* setting stroke-dashoffset to 0 will reveal the hidden checkmark */

[type="checkbox"]:checked+label svg polyline {
  stroke-dashoffset: 0;
}

textarea {
  width: 100%;
  box-sizing: border-box;
  height: 200px;
  resize: none;
  padding: 4px;
  font-size: 16px;
  border: 1px solid #dee2e6;
}

button {
  width: 100%;
  display: block;
  height: 40px;
  background-color: #191516;
  border: none;
  font-weight: bold;
  font-size: 20px;
  color: white;
  box-sizing: border-box;
}

#description {
  text-align: center;
  font-style: italic;
  margin-top: -10px;
}

/* custom circle for radio button*/
[type="radio"]+label::before {
  content: '';
  position: absolute;
  border-radius: 50%;
  background-color: #eee;
  left: -27px;
  top: -1px;
  width: 20px;
  height: 20px;
  transition: all 0.4s;
}

/* fill circle with inset shadow when it is selected*/
[type="radio"]:checked+label::before {
  box-shadow: inset 0 0 0 6px #191516;
}


@media (max-width: 540px) {
main {
      max-width: 380px;
  }
}
@media (max-width: 460px) {
  main {
      max-width: 280px;
  }
  h1 {
      font-size: 32px;
  }
  #description {
      font-size: 15px;
  }
  form {
      font-size: 18px;
  }
  #name, #email, #number, #dropdown, textarea {
      font-size: 15px;
  }
  [type="radio"]+label, [type="checkbox"]+label>span {
      font-size: 15px;
  }
}

@media (max-width: 375px) {
main {
      max-width: 260px;
      padding: 10px 30px 30px 40px;
  }
  h1 {
      font-size: 28px;
  }
  #description {
      font-size: 12px;
  }
  form {
      font-size: 15px;
  }
  #name, #email, #number, #dropdown, textarea {
      font-size: 12px;
  }
  [type="radio"]+label, [type="checkbox"]+label>span {
      font-size: 12px;
  }
  button {
      font-size: 18px;
  }
}

@media (max-width: 320px) {
main {
      max-width: 220px;
  }
}

@media (max-width: 280px) {
main {
      max-width: 180px;
  }
}
